@font-face {
    font-family: 'BentonSans-Regular';
    src: url('../fonts/bentonsansregular/BentonSans\ Regular.otf');
}

@font-face {
    font-family: 'BentonSans-bold';
    src: url('../fonts/bentonsansbold/BentonSans\ Bold.otf');
}

@font-face {
    font-family: "Gotham Book";
    src: url("../fonts/GothamBook/GothamBook.ttf");
}

html,
body {
    font-family: 'BentonSans-Regular';
    width: 100%;
    margin: 0px !important;
}

@media(max-width:767px) {
    html,
    body {
        overflow-x: hidden;
    }
}

.header {
    height: 100vh;
}

.text-color {
    color: #fff;
    text-shadow: 0px 3px 6px #000000CC;
    font-weight: normal;
}

a:hover {
    text-decoration: none;
}

.section-name {
    font-size: 2.25rem;
}

.fancybox-slide--iframe .fancybox-content {
    background-color: #000000 !important;
}

.fancybox-content {
    background-color: #000 !important;
}

.fancybox-button.fancybox-close-small {
    color: #fff !important;
    background-color: #000000 !important;
}

.fancybox-slide {
    padding: 0px !important;
}

.fancybox-toolbar {
    opacity: 1 !important;
    transition: opacity .25s ease 0s, visibility 0s ease 0s !important;
    visibility: visible !important;
}

@media (max-width: 300px) {
    .bg-contact-form .d-flex,
    .social-section {
        flex-wrap: wrap;
        text-align: center;
    }
    .bg-contact-form .d-flex div {
        flex-grow: 1;
        margin-top: 5px !important;
    }
    .bg-agents .d-flex {
        flex-wrap: wrap;
        text-align: center;
    }
    .bg-agents .flex-shrink-0 {
        flex-grow: 1;
        margin-top: 5px !important;
    }
    .bg-agents .my-2 {
        text-align: center;
    }
    .bg-contact .text-white.mb-4 {
        text-align: center;
    }
    .g-recaptcha {
        transform: scale(0.66);
        -webkit-transform: scale(0.66);
        transform-origin: 0 0;
        -webkit-transform-origin: 0 0;
    }
    .visit-my-website-btn {
        margin-top: 10px;
    }
    .sidebar {
        padding: 1.5rem 1rem 0px 4rem !important;
    }
    .sidebar {
        ul {
            padding: 0rem !important;
        }
    }
}

@media (max-width: 1024px) {
    .g-recaptcha {
        transform: scale(0.77);
        -webkit-transform: scale(0.77);
        transform-origin: 0 0;
        -webkit-transform-origin: 0 0;
    }
}

.htmlDiv {
    position: absolute;
    height: 100%;
    width: 100%;
}

.overlap {
    cursor: pointer;
    position: absolute;
    background-color: transparent !important;
    height: 100%;
    width: 100%;
    z-index: 1;
}

.modal-open {
    padding-right: 0px !important;
}

.close:focus {
    outline: none !important;
}

.btn-register {
    border-radius: 0px !important;
}

.btn-register:focus {
    box-shadow: none;
}

// scrollbar design
body::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #F5F5F5;
}

body::-webkit-scrollbar {
    width: 15px;
    background-color: #F5F5F5;
}

body::-webkit-scrollbar-thumb {
    background-color: #000000;
    border: 2px solid #555555;
}

.dates-section::-webkit-scrollbar-track,
.features-discription::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #F5F5F5;
}

.dates-section::-webkit-scrollbar,
.features-discription::-webkit-scrollbar {
    width: 10px;
    background-color: #F5F5F5;
}

.dates-section::-webkit-scrollbar-thumb,
.features-discription::-webkit-scrollbar-thumb {
    background-color: #000000;
    border: 2px solid #555555;
}

.btn:not(:disabled):not(.disabled):active,
.btn:focus {
    box-shadow: none !important;
    outline: none !important;
}

.ps__rail-y {
    opacity: 0.6 !important;
}

.ps:hover>.ps__rail-y,
.ps:hover>.ps__rail-x {
    opacity: 0.6 !important;
}

// iphone media query
@media only screen and (min-device-width: 320px) and (max-device-width: 568px) and (-webkit-device-pixel-ratio: 2) and (device-aspect-ratio: 40/71) {
    .visit-my-website-btn {
        margin-top: 10px;
    }
    .contact-header {
        flex-direction: column;
        text-align: center;
    }
}

.own-own-tooltip {
    position: relative;
    display: inline-block;
}

.own-own-tooltip .tooltiptext {
    visibility: hidden;
    width: 200px;
    background-color: #212121;
    color: #fff;
    text-align: center;
    border-radius: 6px;
    padding: 5px;
    position: absolute;
    z-index: 1;
    bottom: 150%;
    left: 50%;
    margin-left: -100px;
    opacity: 0;
    transition: opacity 0.3s;
}

.own-own-tooltip .tooltiptext::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
}

.own-own-tooltip:hover .tooltiptext {
    visibility: visible;
    opacity: 1;
}

.help-block {
    color: #a94442;
}

.has-error .form-control {
    border-color: #a94442 !important;
}

.has-success .form-control {
    border-color: #3c763d !important;
}

.fancybox-caption {
    text-align: center;
    font-size: 1.2em;
}

.top-bar-shadow {
    z-index: 10 !important;
}

*,
*:focus,
*:hover {
    outline: none;
}