:root {
    --headertop: 0px;
    --galleryHeight: 800px;
}

.bg-header-transparent {
    background-color: rgba($color: #000000, $alpha: 0.4);
}

.gallary-header {
    margin-top: var(--headertop);
}

.bg-gallary {
    background-color: #212121;
}

#section-gallery-collection {
    text-align: center;
}

#section-gallery-collection {
    background-color: #000000;
    min-height: var(--galleryHeight);
    z-index: 1;
    position: relative;
    width: 100%;
}

#section-gallery-collection .gallerycol-list {
    padding-top: 30px;
    padding-bottom: 30px;
}

#section-gallery-collection .gallerycol-list ul {
    display: block;
    list-style: none;
    text-align: center;
    padding-left: 0px;
}

#section-gallery-collection .gallerycol-list ul li {
    display: inline-block;
    padding: 10px;
    position: relative;
}

#section-gallery-collection .gallerycol-list ul li img {
    display: inline;
    border: 1px solid #bdbdbd;
    width: 300px;
    height: 200px;
    transition: all .2s ease-in-out;
    object-fit: cover;
}

#section-gallery-collection .gallerycol-list ul li img:hover {
    transform: scale(1.1);
    // -webkit-box-shadow: 0px 0px 35px -9px rgba(201, 37, 40, 0.8);
    // -moz-box-shadow: 0px 0px 35px -9px rgba(201, 37, 40, 0.8);
    // box-shadow: 0px 0px 35px -9px rgba(201, 37, 40, 0.8);
}

#section-gallery-collection .gallerycol-list ul li .pdf {
    display: inline-block;
    border: 1px solid #bdbdbd;
    width: 300px;
    height: 200px;
    transition: all .2s ease-in-out;
    overflow: hidden !important;
    vertical-align: middle;
    padding: 25% 10%;
    color: #ffffff;
    overflow-wrap: break-word;
}

#section-gallery-collection .gallerycol-list ul li .pdf:hover {
    transform: scale(1.1);
    // -webkit-box-shadow: 0px 0px 35px -9px rgba(201, 37, 40, 0.8);
    // -moz-box-shadow: 0px 0px 35px -9px rgba(201, 37, 40, 0.8);
    // box-shadow: 0px 0px 35px -9px rgba(201, 37, 40, 0.8);
}

.fancybox-bg {
    background: #000000 !important;
    opacity: 1 !important;
}

.gallerycol-backbtn {
    background: url(../assets/cancel_white.png) no-repeat center center rgba(0, 0, 0, 0.8);
    border-radius: 0px;
    top: 8.2rem;
    right: 3rem;
    position: fixed;
    z-index: 9999;
    padding: 8px 21px;
    text-indent: -9999px;
    cursor: pointer;
    background-color: #212121;
    border-color: #212121;
    border: 1px solid #ccc;
}

.gallerycol-backbtn:hover {
    background: url(../assets/cancel_black.png) no-repeat center center;
    background-color: #ffffff !important;
    color: #212121 !important;
    border: 1px solid #212121;
}

.gallerycol-backbtn:focus {
    box-shadow: none;
}

.gallerycol-backbtn:not(:disabled):not(.disabled):active:focus {
    box-shadow: none;
}

@media all and (max-width: 540px) {
    .gallerycol-backbtn {
        top: 11.2rem;
        right: 1rem;
    }
}

@media (max-width:300px) {
    #section-gallery-collection .gallerycol-list ul li img,
    #section-gallery-collection .gallerycol-list ul li .pdf {
        width: 200px;
        height: 200px;
    }
}

@media only screen and (min-device-width: 320px) and (max-device-width: 568px) and (-webkit-device-pixel-ratio: 2) and (device-aspect-ratio: 40/71) {
    #section-gallery-collection .gallerycol-list ul li img,
    #section-gallery-collection .gallerycol-list ul li .pdf {
        width: 250px;
        height: 250px;
    }
}

.fancybox-caption {
    top: 80% !important;
}

#gallery {
    z-index: 990000 !important;
}