//gallary section
.image-gallerybg {
    height: 600px;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
}

.see-more-gallary {
    padding: 0.5rem 1rem;
    border: 2px solid #fff;
    // box-shadow: 3px 0px #ffffff, 0px 3px #ffffff;
    // border-radius: 5px;
}

.gallery-name {
    color: #fff;
    text-shadow: 0px 3px 6px #000000CC;
}

.see-more-gallary:hover {
    color: #fff;
    border-color: #333433;
    text-shadow: none;
    background-color: #333433;
}

// features section
.features-bg {
    background-color: var(--featuresBackground);
}

.features-discription {
    max-height: var(--featuresMaxHeight);
    overflow-y: auto;
    position: relative;
}

@media (min-width:576px) {
    .all-videos {
        padding-top: 3.2rem;
        padding-bottom: 3.2rem;
    }
}

.videos-section {
    width: 100%;
    height: 0;
    position: relative;
    padding-bottom: 56%;
}

.videos-section iframe {
    width: 100%;
    height: 100%;
    position: absolute;
    display: block;
    top: 0;
    left: 0;
}

 :root {
    --smallDeviceamount: 93px;
    --fullHeight: 100px;
    --largeAmount: 88%;
    --mediumAmount: 85%;
    --videoBGColor: #212121;
    --docColor: #212121;
    --featuresMaxHeight: auto;
    --featuresBackground: #f2f2f2;
    --locationTitle: #212121;
}

.features-name {
    color: var(--docColor);
}

.video-section-bg {
    background: linear-gradient(var(--videoBGColor) 0px, var(--videoBGColor) var(--smallDeviceamount), var(--featuresBackground) var(--smallDeviceamount), var(--featuresBackground) var(--fullHeight));
}

@media(min-width:576px) {
    .video-section-bg {
        background: linear-gradient( to right, var(--videoBGColor) 0%, var(--videoBGColor) var(--mediumAmount), var(--featuresBackground) var(--mediumAmount), var(--featuresBackground) 100%);
    }
}

@media(min-width:813px) {
    .video-section-bg {
        background: linear-gradient( to right, var(--videoBGColor) 0%, var(--videoBGColor) var(--largeAmount), var(--featuresBackground) var(--largeAmount), var(--featuresBackground) 100%);
    }
}

.update-doc-color {
    color: var(--docColor);
}

.location-title h2 {
    color: var(--locationTitle);
    text-shadow: none;
}

.location-section {
    position: relative;
}

.document {
    position: absolute;
}

.map-section {
    width: 100%;
    height: 10rem;
}

.show-full-map {
    color: #ffffff;
    z-index: 1;
    background-color: #333333;
    margin: 5px;
    padding: 0px 5px;
}

.document-download {
    cursor: pointer;
}

.document-open {
    width: 100%;
    height: 100%;
}

.remove-scroll-body {
    overflow-y: hidden;
}

.map-external-link {
    background-color: rgba(108, 117, 125, 0.5) !important
}

.map-external-link:hover {
    background-color: rgba(108, 117, 125, 0) !important
}

.show-full-map .fa-external-link-alt {
    color: #0056B8 !important;
}

// floor plan section
.floorplan-title h2 {
    color: #333333;
    text-shadow: none;
}

.lSAction>a {
    background-image: none;
}

.lSAction>.lSPrev {
    opacity: 0.8;
    left: -10px;
}

.lSAction>.lSNext {
    opacity: 0.8;
    right: -20px;
}

.lSAction>.lSPrev::after {
    content: url('../assets/left-arrow.png');
}

.lSAction>.lSNext::after {
    content: url('../assets/right-arrow.png');
}

.lSSlideWrapper {
    position: unset;
}

.floor-plan-1 {
    position: relative;
}

.floor-plan-pdf {
    position: relative;
}

.floor-plan-images {
    width: 100%;
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height: 350px;
    cursor: pointer;
}

.border-black-plan {
    cursor: pointer;
}

//schedule section
.bg-schedule {
    background-color: gray;
    max-height: 1000px;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    padding-bottom: 5rem;
}

@media (max-width:576px) {
    .bg-schedule {
        max-height: fit-content !important;
    }
}

.schedule-title {
    color: #fff;
    font-weight: bold;
}

.schedule-line {
    height: 1px;
    border: 1px solid #fff;
}

.showing-section {
    background-color: #fff;
}

.online-showing-section {
    background-color: #F9F9F9;
}

.zoom-logo {
    width: 100px;
}

.Showing-date-section,
.online-Showing-date-section {
    position: relative;
}

.dates-section {
    max-height: 240px;
    overflow-y: auto;
}

.single-showing-section {
    max-height: 300px;
    overflow-y: auto;
}

.single-showing-section .dates-times {
    min-width: 350px;
}

@media (max-width:1024px) {
    .single-showing-section .dates-times {
        min-width: 250px;
    }
}

@media (max-width:768px) {
    .dates-section {
        height: auto;
        max-height: none;
    }
}

.dates,
.times {
    color: #333333;
}

.dates-times {
    border-bottom: 1px solid #cccccc;
}

.single-showing-section .dates-times {
    border-bottom: 1px solid #cccccc;
}

.dates-section .dates-times:nth-last-child(3) {
    border-bottom: 0;
}

.join-btn {
    border-radius: 0px;
}

.policy-link {
    text-decoration: underline;
}

.zoom-section-last {
    position: absolute;
    bottom: 1.5rem;
    width: 100%;
}

.border_bottom_zero {
    border-bottom: 0 !important;
}

// contact section
.bg-contact {
    min-height: 750px;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    background-color: gray;
}

.bg-contact-form {
    background-color: rgba(51, 51, 51, 1);
}

.bg-agents {
    background-color: rgba(51, 51, 51, 1);
    margin-bottom: 1.8rem;
}

.transparent-input {
    background-color: rgba(0, 0, 0, 0) !important;
    border-radius: 0px !important;
    color: #ffffff !important;
}

.g-capcha-validation {
    font-size: 80% !important;
}

.show-agent-email {
    word-wrap: break-word;
}

.social-icons img {
    width: 24px;
    height: 24px;
}

@media (max-width:768px) {
    .transparent-input {
        border-color: #FFFFFF80 !important;
    }
}

.resize-none {
    resize: none;
}

.linked-in,
.twitter {
    border-radius: 3px;
}

.social-section {
    border-top: 1px solid rgba(255, 255, 255, 0.5);
}

.transparent-input:focus {
    box-shadow: none !important;
}

.submit-box {
    width: 40px;
    height: 30px;
}

.btn_submit {
    border: 1px solid transparent;
    padding: 1.25rem 3rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: 2px;
    background-color: #000000 !important;
    color: #ffffff;
    background: url(../assets/plane_white.png) no-repeat center center rgba(0, 0, 0, 0.8);
}

.btn_submit:hover {
    background-color: #ffffff !important;
    color: #000000 !important;
    background: url(../assets/plane_black.png) no-repeat center center rgba(0, 0, 0, 0.8);
}

.about-agent {
    padding: 5px 10px;
    border: 1px solid #fff;
}

.about-agent:hover {
    background-color: #fff !important;
    border: 1px solid #000000;
    color: #000000 !important;
}

.agent-info {
    color: rgba(255, 255, 255, 0.7) !important;
}

.agent-logo {
    width: 50px;
}

.agent-header {
    border-bottom: 0px;
}

.agent-header .close {
    margin: 0rem 0rem 0rem auto !important;
}

.visit-my-website-btn {
    border-radius: 0px;
}

.visit-my-website-btn:focus {
    box-shadow: none;
}

// footer section
.back-to-top {
    background: url(../assets/up-white.png) no-repeat center center rgba(0, 0, 0, 0.8);
    border: 1px solid #ccc;
}

.back-to-top {
    right: 3rem;
    bottom: 3rem;
    position: fixed;
    padding: 8px 21px;
    text-indent: -9999px;
    cursor: pointer;
    z-index: 1;
    background-color: #212121 !important;
    color: #ffffff;
    display: none;
}

@media all and (max-width: 540px) {
    .back-to-top {
        right: 1rem;
    }
}

.back-to-top:hover {
    background-color: #ffffff !important;
    color: #212121 !important;
    border: 1px solid #212121;
    background: url(../assets/up-black.png) no-repeat center center;
}

.bg-footer {
    background-color: #212121;
}

.footer-section {
    border-top: 1px solid rgba(255, 255, 255, 0.5);
}

.footer-text {
    font-size: 0.8rem;
}

.footer-logo {
    width: 10rem;
}

// document style
.sidenav-left {
    height: 100%;
    width: 0;
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background-color: #111;
    overflow-x: hidden;
    padding-top: 60px;
    z-index: 9999;
    background: center/cover;
    transition: all 1s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.sidenav-right {
    height: 100%;
    width: 0;
    position: fixed;
    top: 0;
    right: 0;
    background-color: #111;
    overflow-x: hidden;
    padding-top: 60px;
    z-index: 9999;
    transition: all 1s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.sidenav-right .closebtn {
    position: absolute;
    top: 0;
    right: 25px;
    font-size: 3rem;
    margin-left: 50px;
    color: #fff;
}

@media screen and (max-height: 450px) {
    .sidenav {
        padding-top: 15px;
    }
    .sidenav a {
        font-size: 18px;
    }
}