.sidebar {
    position: fixed;
    right: 0;
    top: 0;
    width: 20rem;
    height: 100vh !important;
    max-height: 100vh !important;
    background: rgba(52, 58, 64, 0.8);
    padding: 0.5rem 1rem;
    font-size: 1.2em;
    text-transform: uppercase;
    z-index: 9999;
    display: none;
    &.visible {
        display: block;
    }
    transition: all 1s cubic-bezier(0.645, 0.045, 0.355, 1);
}

.sidebar .nav-link {
    text-align: center;
    color: $light;
    font-weight: 300;
    display: inline-block;
    position: relative;
    margin-bottom: 0.5rem;
    padding: 0.5rem;
    &:after {
        content: " ";
        position: absolute;
        bottom: 0;
        left: 0;
        width: 0;
        height: 2px;
        background: $light;
        transition: all 1s cubic-bezier(0.645, 0.045, 0.355, 1);
    }
    &:hover {
        &:after {
            width: 100%;
        }
    }
}

.sidebar {
    ul {
        display: block;
        margin: 0;
        padding: 1rem;
        li {
            display: block;
            margin: 0;
            padding: 0;
            text-align: center;
        }
    }
}

#scrollbar {
    position: relative;
    height: 100%;
    max-height: 80vh !important;
}