.bg-header {
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    position: relative;
}

.dark-bg {
    background: #000000;
    position: absolute;
    width: 100%;
    height: 100%;
}

.header-logo {
    max-height: 4rem;
    max-width: 8rem;
    margin-right: 1rem;
}

.bottom-header {
    position: absolute;
    bottom: 0;
    width: 100%;
    z-index: 2;
}

.property-details h4 {
    letter-spacing: 1px;
    font-weight: 400;
    font-size: 1.3rem;
}

.top-bar-style {
    z-index: 1;
    background: transparent linear-gradient(180deg, #333333 0%, #333333 10%, #38383800 100%) 0% 0% no-repeat padding-box;
}

.bottom-header-style {
    background: transparent linear-gradient(0deg, #333333 0%, #333333 10%, #38383800 100%) 0% 0% no-repeat padding-box;
}

@media(max-width: 767px) {
    .property-details h4 {
        font-size: 1rem;
    }
    .property-details h1 {
        font-size: 2rem;
    }
    .agent-name {
        font-size: 0.75rem;
    }
    .plus-minus {
        font-size: 1.3rem !important;
    }
    .top-bar-shadow {
        background: none;
    }
}

.custom-toggler.navbar-toggler {
    border-color: #fff;
    border-radius: 0px;
}

.custom-toggler .navbar-toggler-icon {
    background-image: url("data:image/svg+xml;charset=utf8,%3Csvg viewBox='0 0 32 32' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(255, 255, 255, 1)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 8h24M4 16h24M4 24h24'/%3E%3C/svg%3E");
}

.plus-minus {
    font-size: 1.5rem;
    font-family: unset;
    font-weight: 400;
}

.navbar-collapse {
    background-color: rgb(255, 255, 255);
    z-index: 1;
    padding-left: 10px;
    text-align: center;
    font-weight: bold;
}

.tv {
    position: absolute;
    top: 0;
    left: 0;
    // z-index: 1;
    width: 100%;
    height: 100%;
    overflow: hidden;
    animation-fill-mode: none !important;
}

.tv .screen.active {
    opacity: 1;
}

.tv .screen {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1 !important;
    margin: auto;
    opacity: 0;
    animation-fill-mode: none !important;
    -webkit-transition: opacity 0.5s;
    -o-transition: opacity 0.5s;
    transition: opacity 0.5s;
}

.tv iframe {
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
}

.light-bg-navbar {
    background-color: rgba(0, 0, 0, 0.2);
}

#youtube-video {
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.youtube-thumbnail {
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
}

#slideshow {
    position: absolute !important;
}

#slides {
    position: absolute !important;
    width: 100%;
    height: 100vh;
}